.popup {
    width: 100%;
    height: 100%;
    position: fixed;
    align-items: center;
    justify-content: center;
    left: 0;
    top: 0;
    display: flex;
    background: rgba(0, 0, 0, .5);
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s, opacity .2s linear;
}